<template>
    <div id="register-main-container">
        <div id="register-head">
            <div id="register-back-img-wrap">
                <img id="register-back-img" src="@/assets/nightmode.png" width="300px">
            </div>
        </div>
        <div id="register-body-container">
            <div id="register-body-line">
            </div>
            <span id="register-body-title">注册</span>
            <div id="username-input-container">
                <input type="text" autocomplete="off" name="username" placeholder="昵称" id="username-input" @change="getUsername($event)" maxlength=24>
            </div>
            <div id="password-input-container">
                <input type="text" onfocus="this.type='password'" autocomplete="off" name="password" placeholder="密码" id="password-input" @change="getPassword($event)" maxlength=10>
            </div>
            <div id="password-input-container2">
                <input type="text" onfocus="this.type='password'" autocomplete="off" name="password2" placeholder="再次输入" id="password-input2" @change="getPassword2($event)" maxlength=10>
            </div>
            <button type="button" id="register-button-" @click="register()">
                <span>注册</span>
            </button>
        </div>
    </div>
</template>

<style>
#register-main-container{
    background-color: #f6f9fa;
    height: 970px;
    width: 100%;
}
#register-head{
    background-color: white;
    width: 1000px;
    height: 170px;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    margin: 0 auto;
}
#register-back-img-wrap{
    width: 1000px;
    height: 170px;
    background-color: black;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
}
#register-back-img{
    position: absolute;
    top: 20px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 auto;
}

#register-body-container{
    background-color: white;
    width: 960px;
    height: 455px;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 190px;
    margin: 0 auto;
    padding: 20px;
    padding-top: 45px;
}
#register-body-line{
    font-size: 25px;
    color: black;
    border-bottom: 1px solid #ddd;
    margin-bottom: 30px;
}
#register-body-title{
    font-size: 38px;
    color: black;
    background-color: white;
    position: absolute;
    left: 457px;
    top: 15px;
    width: 86px;
}
#username-input-container{
    font-size: 14px;
    width: 420px;
    height: 40px;
    position: absolute;
    left: 274px;
    top: 150px;
}
#username-input{
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    color: #606266;
    font-size: inherit;
    height: 40px;
    line-height: 40px;
    outline: 0;
    padding: 0 15px;
    width: 100%
}
#password-input-container{
    font-size: 14px;
    width: 420px;
    height: 40px;
    position: absolute;
    left: 274px;
    top: 232px;
}
#password-input{
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    color: #606266;
    font-size: inherit;
    height: 40px;
    line-height: 40px;
    outline: 0;
    padding: 0 15px;
    width: 100%
}
#password-input-container2{
    font-size: 14px;
    width: 420px;
    height: 40px;
    position: absolute;
    left: 274px;
    top: 314px;
}
#password-input2{
    background-color: #fff;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    color: #606266;
    font-size: inherit;
    height: 40px;
    line-height: 40px;
    outline: 0;
    padding: 0 15px;
    width: 100%
}
#register-button-{
    color: #fff;
    background-color: black;
    line-height: 1;
    white-space: nowrap;
    border: 1px solid #dcdfe6;
    text-align: center;
    font-weight: 500;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 4px;
    position: absolute;
    left: 274px;
    top: 396px;
    width: 452px;
    height: 42px;
}
</style>

<script>

export default {
    name:'register',
    data(){
        return{
            'username':'',
            'password':'',
            'password2':'',
        }
    },

    methods:{
        getUsername:function(e){
            this.username = e.target.value
        },
        getPassword:function(e){
            this.password = e.target.value
        },
        getPassword2:function(e){
            this.password2 = e.target.value
        },
        register:function(){
            if(this.password!=this.password2){
                this.$message({
                    type:'info',
                    message:'两次输入密码不一致！'
                })
                return
            }
            fetch(this.$store.state.backend + '/register', {
                method: 'POST',
                mode:'cors',
                body: JSON.stringify({
                    'nickname': this.username,
                    'password': this.password,
                    'sex': 'Unknown'
                })
            }).then(res => res.text())
              .then(data => {
                this.$message({
                    type:'info',
                    message:data
                })
                if(data=='注册成功！'){
                    this.$router.push('/login')
                }
              }).catch(err =>{
                this.$message({
                    type:'info',
                    message:'网络连接失败'
                })
              })
        }
    },
    created(){
    }
}
</script>
